    margin: 50px;
}

.cloud {
    background: #182533;
    border-radius: 10px 10px 10px 0;
    padding: 5px 15px 5px 15px;
    position: relative;
    max-width: 450px;
}

.cloud:before {
    position: absolute;
    content: '';
    bottom: 0;
    left: -10px;
    width: 10px;
    height: 10px;
    background-color: #182533;
}

.cloud:after {
    position: absolute;
    content: '';
    bottom: 0;
    left: -10px;
    width: 10px;
    height: 10px;
    background-color: #2D8CFF;
    border-radius: 0 0 100% 0;
}

.cloud_name {
    color: #429BDB;
    font-size: 17px;
}

.cloud_time {
    color: #6D7F8F;
    font-size: 16px;
    text-align: right;
}

.avatar {
    display: flex;
    align-self: flex-end;
}

.avatar img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 15px;
    background-color: #fff;
    background-size: 32px;
}

.cloud_text p span {
    font-size: 15px;
    color: #6D7F8F;
    float: right;
}

